.screen {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.frame {
  width: 30rem;
  height: 45rem;
  max-height: 100vh;
}

@media screen and (max-width: 1024px) {
  .frame {
    width: 100%;
    height: 90vh;
  }
}
